body, p {
  font-family: "brandon-grotesque",sans-serif;
  font-style: normal;
  font-weight: 400; }

.home-text, .pac-desc, .light {
  font-family: "brandon-grotesque",sans-serif;
  font-style: normal;
  font-weight: 300; }

.tester-btn, .pac-list-li, .bold {
  font-family: "brandon-grotesque",sans-serif;
  font-style: normal;
  font-weight: 700; }


h1,.nav-li, .pac-head, .cotact-label, .contact-info-label, .detail-title, .detail-client-head {
  font-family: 'Kumar One', cursive;
  font-style: normal;
  font-weight: 400; }

body, p {
  font-size: 20px;
  @media screen and (max-width: $sm-phone) {
    font-size: 16px; } }


